@import url(https://fonts.googleapis.com/css?family=Ubuntu+Condensed);
@import url(https://fonts.googleapis.com/css?family=Ubuntu+Condensed);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

html {
	overflow: hidden;
}
body {
	padding-top: 2px;
}

body::after {
	content: '';
	width: 200%;
	height: 200%;
	top: -50%;
	left: -50%;
	bottom: 0;
	right: 0;
	position: fixed;
	z-index: -1;
	background-image: url(/colorPicker/static/media/bg-img.0f0dda1b.jpg);
	background-position: center center;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;
	opacity: 0.7;
}

.App-set {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
}

.App {
	text-align: center;
	height: 80vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.App-cont {
	margin: 0 5em;
	padding: 10px 40px;
	border-radius: 20px;
	box-shadow: inset 1px 1px 4px rgba(34, 60, 80, 0.4),
		0px 6px 5px 0px rgba(34, 60, 80, 0.4),
		12px 12px 12px -10px rgba(34, 60, 80, 0.4);
}

input {
  padding: 0;
  margin: 0;
  border: 0;
  border: none;
  outline: none;
}
.btn {
  background-color: rgba(34, 60, 80, 0.4);
  font-family: "Ubuntu Condensed", sans-serif;
  letter-spacing: 2px;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  color: rgb(240, 240, 240);
  position: relative;
  padding: 10px 40px;
  margin: 1em;
  border-radius: 20px;
  box-shadow: inset 1px 1px 1px rgba(23, 40, 53, 0.4),
    0px 6px 2px 0px rgba(23, 40, 53, 0.4),
    12px 12px 12px -10px rgba(23, 40, 53, 0.4);
}

.btn:active {
  top: 3px;
  box-shadow: inset 0px 1px 0px rgba(23, 40, 53, 0.4),
    0px 2px 0px 0px rgba(23, 40, 53, 0.4), 0px 5px 3px rgba(23, 40, 53, 0.4);
}

.btn:active::before {
  top: 50%;
  margin-top: -12px;
  box-shadow: inset 0px 1px 0px rgba(23, 40, 53, 0.4),
    0px 3px 0px rgba(23, 40, 53, 0.4), 0px 6px 3px rgba(23, 40, 53, 0.4);
}

input[type="range"] {
  -webkit-appearance: none;
  margin-top: 10px;
  width: calc(250px + 2vmin);
  background: transparent;
}
input[type="range"]:focus {
  outline: none;
}
input[type="range"]::-webkit-slider-runnable-track {
  width: calc(250px + 2vmin);
  height: 15px;
  cursor: pointer;
  background: rgb(243, 114, 255);
  border-radius: 14px;
}
input[type="range"]::-webkit-slider-thumb {
  border: 1px solid rgb(243, 114, 255);
  height: 20px;
  width: 20px;
  border-radius: 50px;
  background: rgb(253, 253, 253);
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -2.5px;
}
input[type="range"]:focus::-webkit-slider-runnable-track {
  background: rgb(243, 114, 255);
}
input[type="range"]::-moz-range-track {
  width: calc(250px + 2vmin);
  height: 15px;
  cursor: pointer;
  background: rgb(243, 114, 255);
  border-radius: 14px;
}
input[type="range"]::-moz-range-thumb {
  height: 20px;
  width: 20px;
  border-radius: 50px;
  background: rgb(243, 114, 255);
  cursor: pointer;
}
input[type="range"]::-ms-track {
  width: calc(250px + 2vmin);
  height: 15px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type="range"]::-ms-fill-lower {
  background: rgb(243, 114, 255);
  border-radius: 28px;
}
input[type="range"]::-ms-fill-upper {
  background: rgb(243, 114, 255);
  border-radius: 28px;
}
input[type="range"]::-ms-thumb {
  height: 20px;
  width: 20px;
  border-radius: 50px;
  background: rgb(243, 114, 255);
  cursor: pointer;
}
input[type="range"]:focus::-ms-fill-lower {
  background: rgb(243, 114, 255);
}
input[type="range"]:focus::-ms-fill-upper {
  background: rgb(243, 114, 255);
}

.range-cont {
  height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  background-color: transparent;
}

.range-right-cont {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h2 {
  margin-bottom: 1em;
  color: #f7f7f7;
  font-family: "Roboto Slab", serif;
}

h3 {
  font-size: calc(10px + 2vmin);
  font-family: "Roboto Slab", serif;
  color: rgb(250, 250, 250);
}

.Main {
  height: 70vh;
  width: auto;
}

h1 {
  margin: 0;
  padding: 0;
  font-family: "Roboto Slab", serif;
  text-transform: uppercase;
  color: rgb(255, 255, 255);
  font-size: calc(40px + 2vmin);
  text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9,
    0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1),
    0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2),
    0 5px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.2),
    0 20px 20px rgba(0, 0, 0, 0.15);
}

.Main-text {
  color: #120929;
  font-size: calc(10px + 2vmin);
  padding: 0 1em;
}

.menu-main {
	list-style: none;
	margin: 1em 0 5px;
	padding: 25px 0 5px;
	text-align: center;
	background: transparent;
}
.menu-main li {
	display: inline-block;
}
.menu-main li:after {
	content: '|';
	color: #ffffff;
	display: inline-block;
	vertical-align: top;
}
.menu-main li:last-child:after {
	content: none;
}
.menu-main a {
	text-decoration: none;
	font-family: 'Ubuntu Condensed', sans-serif;
	letter-spacing: 2px;
	position: relative;
	padding-bottom: 20px;
	margin: 0 34px 0 30px;
	font-size: calc(10px + 2vmin);
	text-transform: uppercase;
	display: inline-block;
	transition: color 0.2s;
}
.menu-main a,
.menu-main a:visited {
	color: #f5f4f5;
}
.menu-main a.current,
.menu-main a:hover {
	color: rgb(243, 114, 255);
}
.menu-main a:before,
.menu-main a:after {
	content: '';
	position: absolute;
	height: 4px;
	top: auto;
	right: 50%;
	bottom: -5px;
	left: 50%;
	background: rgb(243, 114, 255);
	transition: 0.8s;
}
.menu-main a:hover:before,
.menu-main .current:before {
	left: 0;
}
.menu-main a:hover:after,
.menu-main .current:after {
	right: 0;
}
@media (max-width: 550px) {
	.menu-main {
		padding-top: 0;
	}
	.menu-main li {
		display: block;
	}
	.menu-main li:after {
		content: none;
	}
	.menu-main a {
		padding: 25px 0 20px;
		margin: 0 30px;
	}
}

